.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}
.cardStyle {
    background-color: #f8f5de;
    background-image: -webkit-linear-gradient(left, rgba(255, 210, 0, 0.4), rgba(200, 160, 0, 0.1) 11%, rgba(0, 0, 0, 0) 35%, rgba(200, 160, 0, 0.1) 65%);
    background-image: linear-gradient(to right, rgba(255, 210, 0, 0.4), rgba(200, 160, 0, 0.1) 11%, rgba(0, 0, 0, 0) 35%, rgba(200, 160, 0, 0.1) 65%);
    margin: 0.7rem;
    border-radius: 0.2rem;
    position: relative;
    width: 32.5rem;
    box-shadow: inset 0 0 7.5rem rgba(255, 210, 0, 0.3), inset 0 0 2rem rgba(255, 210, 0, 0.4), inset 0 0 3rem rgba(220, 120, 0, 0.8);
    text-align: left;
    color: rgba(0, 0, 0, 0.5);
    letter-spacing: 0.05rem;
    line-height: 1.8; 
    position: relative;
	}

.imgContainer {
    height: 16rem; 
    width: 100%; 
    text-align: center;
    overflow: hidden;
	}

.img {
	width: 70%;
	}

.content {
    padding-left: 1rem; 
    padding-right: 1rem; 
    position: absolute;
    right: 0rem;
    top: 11rem;
    font-family: 'Cardo', serif;
    text-transform: uppercase;
    font-size: 3rem;
    color: #040F0F;
    text-shadow: .1rem .1rem .1rem #f8f5de, .3rem .3rem .1rem #040F0F;
	}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

